tr {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100vw;
    height: auto;
    align-items: center;
    /* background-color: darkgray; */
    /* background-color: rgba(32, 178, 171, 0.602) ; */
    background-color: rgba(1, 39, 36, 0.452);
    font-size: 18px;
    border: 1px solid black;
}

th, td {
    height: 125px;
    padding: 10px;
    text-align: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 14.285714285714286%;
    height: 100%;
    background-color: rgba(1, 39, 36, 0.452);
    /* background-color: rgba(32, 178, 171, 0.602) ; */
    color: aliceblue;
    text-align: center;
}

#counter {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    box-sizing: border-box;
    width: 7.285714285714286%;
}

#counter.item {
    box-sizing: border-box;
    width: 33.333%;
}

