.Toggle {
    position: relative;
    display: flex;
    justify-content: space-around;
    background-color: transparent;
    border-radius: 25% 25% 0% 0%;
    color: whitesmoke;
    box-sizing: border-box;
    border: 0px;
    border-style: outset;
    transition: all .5s ease-in;
    width: 48px;
    height: 48px;
    /* transition: "width 2s ease-in-out 0.5s", */
}

.ToggleRotate {
    display: flex;
    justify-content: space-around;
    background-color: lightseagreen;
    /* opacity: 0.3; */
    border-radius: 25% 25% 0% 0%;
    color: white;
    box-sizing: border-box;
    border: 0px;
    transition: all .5s ease-out;
    transform: rotate(360deg);
    width: 48px;
    height: 48px;
    /* transition: "width 2s ease-in-out 0.5s", */
}

.ToggleActive {
    position: relative;
    display: flex;
    justify-content: space-around;
    background-color: lightseagreen;
    /* opacity: 0.3; */
    border-radius: 25% 25% 0% 0%;
    color: white;
    box-sizing: border-box;
    border: 0px;
    transition: all .5s ease-out;
    /* transform: rotate(360deg); */
    width: 48px;
    height: 48px;
    /* transition: "width 2s ease-in-out 0.5s", */
}

.row{
    position: fixed;
    bottom: 5px;
    right: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: blue;
    color: white;
    box-sizing: border-box;
    border-radius: 0px 25px 25px 0px;
    padding: 10px;
    width: 150px;
    /* height: 32px; */
}

.row, a {
    text-decoration: none;
}