#right-panel {
    background-color: #f5f5f5;
    display: block;
    position: relative;
    width: 75%;
    height:90%;
    font-family: "Roboto", "sans-serif";
    line-height: 30px;
    border-width: 2px;
    padding: 10px;
    z-index: 1;
    overflow-y: scroll;
  
  }
  
  #right-panel select, #right-panel input {
    font-size: 15px;
  }
  
  #right-panel select {
    width: 98%;
  }
  
  #right-panel i {
    font-size: 12px;
  }
  
  html, body {
    height: 100%;
    margin: 0;
    padding: 0;
  }
  
  #directions-panel {
    width: 98%;
    margin-top: 10px;
    background-color: #ccf5fa;
    overflow-y: scroll;
    height: auto;
  }
  
  #waypoints {
    height:auto;
  
    overflow-x: scroll;
  }
  
  #actions{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: stretch;
  }
  #setWaypoints{
  width: 50%;
  }
  #setDirections{
    width: 50%;
  }