.item {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 25px 0px 25px 0px;
    text-transform: uppercase;
    border: 0px;
    line-height: 36px;
    font-size: 18px;
    transition: all .5s ease-in;
    /* background-color: #a8ccd7; */


}

.item:hover {
    /* opacity: 1; */
    background-color: lightseagreen;
    color:white;
    transition: all .5s ease-out;

}