.container {
    box-sizing: border-box;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    /* padding: 10px 10px 10px 10px; */
    flex-wrap: wrap;
    justify-content: flex-start;
    overflow-y: scroll;
}

.cart--container {
    padding: 5px;
   position: relative;
    box-sizing: border-box;
    width: 100%;
    /* height: 90%; */
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    /* border-radius: 25%; */
    /* background-color: whitesmoke; */
}
.cart--container--desktop {
    padding: 5px;
    position: relative;
    box-sizing: border-box;
    width: 25%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    /* overflow-y: scroll; */
}

.cart--img {
    box-sizing: border-box;
    width: 100%;
    object-fit: scale-down;
    border-radius: 10px 10px 0px 0px;
}
/* .cart--img--revere {
    box-sizing: border-box;
    width: 100%;
    object-fit: scale-down;
    border-radius: 10px 10px 0px 0px;
} */

.cart--info {
    background-color: whitesmoke;
    box-sizing: border-box;
    width: 100%;
    max-height: 75px;
    display: flex;
    flex-direction: row;
    justify-content: stretch;
}
.cart--info-reverse {
    background-color: whitesmoke;
    box-sizing: border-box;
    width: 100%;
    max-height: 75px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.cart--info--item {
    box-sizing: border-box;
    width: 100%;
    text-align: center;
}

.cart--actions {
    background-color: whitesmoke;
    box-sizing: border-box;
    width: 100%;
    /* max-height: 75px; */
    display: flex;
    flex-direction: row;
    /* justify-content: space-around; */
    justify-content: flex-end;
    border-radius: 0px 0px 10px 10px;
    
}
.cart--actions-reverse {
    position: fixed;
    bottom: 0;
    background-color: whitesmoke;
    box-sizing: border-box;
    width: 100%;
    /* max-height: 75px; */
    display: flex;
    flex-direction: row;
    /* justify-content: space-around; */
    justify-content: flex-end;
    border-radius: 0px 0px 0px 0px;
    
}

.cart--actions--item {
    box-sizing: border-box;
    width: max-content;
}

.cart--actions--info {
    box-sizing: border-box;
    width: max-content;
}

.cart--reverse--button {
    position: absolute;
    top: 7px;
    right: 7px;
    color: black;
    font-size: 32;
}
.cart--item--name-reverse {
    position: absolute;
    top: 7px;
    left: 7px;
    color: black;
    font-size: 18px;
}


.cart--reverse--button:hover {
    position: absolute;
    top: 7px;
    right: 7px;
    color: lightseagreen;
    font-size: 32;
}