@keyframes bannermove {
    0% {
        transform: translate(-100%, 0);
    }
    100% {
        transform: translate(100%, 0);
    }
}
@keyframes bannermove2 {
    0% {margin-left: +100vw;}
    0% {margin-right: 0%;}
    100% {margin-left: -300vw;}
    /* 100% {margin-right: 100%;} */
    
}

@keyframes animatedImage {
    0% {
        left: 2000px;
    }
    100% {
        left: -2000px;
    }
}